<template>
  <div class="addressMana">
    <div class="user-header">
      <a class="nav-item1" @click="addressBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="address">地址管理</a>
    </div>
    <div class="managementMain">
      <div class="management" id="management" @touchmove="move" @touchstart="start" v-for="(item,index) in addressList">
        <div class="consignee"><a>收货人：<span>{{item.consignee}}</span></a><a>{{item.mobile}}</a></div>
        <div class="shipping"><a>收货地址：<span>{{item.province + item.city + item.district + item.address}}</span></a></div>
        <div class="defaultAddress">
          <p class="default"><input :checked="item.is_default==1?true:false" @click="defaultInput(item)" name="default" type="radio" value="0"/>默认地址</p>
          <p class="modify" @click="changaddress(item)">修改<img src="../../assets/images/thebar/bc_25.png"/></p>
        </div>
        <div class="delete" @click="deleteAddress(item,index)">删除</div>
      </div>
      <div class="add">
        <img @click="newaddress" src="../../assets/images/thebar/wh_47.png"/>
        <a>新增地址信息</a>
      </div>
    </div>
    <myModal text_l="不删了" text_r="删了" mes="确定删除收获地址?" v-on:childSay="listenToMyBoy" v-on:deletesure="deletesure" v-show="modalShow"></myModal>
  </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;
  function getCookie(name) {
    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  }
  function setCookie(name, value, days) {
    var d = new Date;
    d.setTime(d.getTime() + 24*60*60*1000*days);
    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  }
  import Modal from '../modals/modalStart.vue'
  export default {
    data() {
      return {
        modalShow:false,
        addressList:[],
        startX:0,
        startY:0,
        moveEndX:0,
        moveEndY:0,
        X:0,
        Y:0,
        id:'',
        idx:'',
        p_id:null,
        c_id:null,
        d_id:null,
        isDefault:null,
      }
    },
    components: {
      myModal:Modal
    },
    mounted:function () {
      var _this = this;
      var userId = getCookie("userId");
      var p_id = this.$route.params.p_id;
      var c_id = this.$route.params.c_id;
      var d_id = this.$route.params.d_id;
      $.ajax({
        type: 'GET',
        url: urlhost+"useraddress/updateaddress?user_id="+userId,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var mess = JSON.parse(response);
          _this.addressList = mess.message;
          console.log(mess.message);
        },
      });

    },
    methods:{
      listenToMyBoy:function () {
        this.modalShow = false;
        $('.delete').css("width",0)
      },
      deletesure:function () {
        var _this = this;
        _this.modalShow = false;
        $.ajax({
          type: 'GET',
          url: urlhost+"useraddress/deladdress?id="+_this.id,
          data$Type: "jsonp",
          data: {
          },
          success: function (response) {
            var mess = JSON.parse(response);
            if (mess.status == 0) {
              _this.$layer.msg(mess.message)
            }else{
              _this.$layer.msg(mess.message);
            _this.addressList.splice(_this.idx, 1);
            $('.delete').css("width",0)
            }
          },
        });
      },
      start:function (e) {
        var _this = this
        _this.startX = e.targetTouches[0].pageX;
        _this.startY = e.targetTouches[0].pageY;
//        e.preventDefault();
      },
      move:function (e) {
        var _this = this;
        _this.moveEndX = e.targetTouches[0].pageX,
          _this.moveEndY = e.targetTouches[0].pageY,
          _this.X = _this.moveEndX - _this.startX,
          _this.Y = _this.moveEndY - _this.startY;
        if ( Math.abs(_this.X) > Math.abs(_this.Y) && _this.X > 0 ) {
          $(e.currentTarget.children[3]).animate({'width':0},100)
        }
        else if ( Math.abs(_this.X) > Math.abs(_this.Y) && _this.X < 0 ) {
         $(e.currentTarget.children[3]).animate({'width':'1.5rem'},100)
        }
      },
      deleteAddress:function (data,index) {
        var _this = this;
        _this.modalShow = true;
        _this.id = data.id;
        _this.idx = index;
      },
      defaultInput:function (data) {
        var _this = this;
        var userId = getCookie("userId");
        $.ajax({
          type: 'GET',
          url: urlhost+"useraddress/editdefault?user_id="+userId+"&id="+data.id,
          data$Type: "jsonp",
          data: {
          },
          success: function (response) {
            var mess = JSON.parse(response);
            if(mess.status == 1){
              _this.$layer.msg(mess.message)
            }
          },
        });
      },
      changaddress:function (data) {
        console.log(data)
        this.$router.push({
          name:"changeAddress",
          params:{
            id : data.id,
            province : data.province,
            city : data.city,
            district : data.district,
            detailAdd : data.address,
            mobile : data.mobile,
            username : data.consignee,
          }
        });
      },
      addressBack:function () {
        this.$router.go(-1);
      },
      newaddress:function () {
        this.$router.push({path:"/usercenter/newAddress"})
      }
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .addressMana{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%; 
    overflow: scroll;
    background-color: #f2f2f2;
    .user-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      line-height: n(80);
      z-index: 999;
      border-bottom: 1px solid #d8d8d8;
      .nav-item1{
        position: absolute;
        left: 0.2rem;
        .game_1{
          width: n(50);
          height: n(50);
        }
      }
      .address{
        font-size: 14px;
        font-weight: bold;
        width: 100%;
        display: inline-block;
        text-align: center;
      }
    }
    .managementMain{
      width: 95%;
      height: 80%;
      margin-top: n(80);
      margin-left: 2.5%;
      margin-bottom: 10%;
      overflow: scroll;
      background: #f2f2f2;
      .management{
        position: relative;
        margin-top: n(20);
        padding: 10px;
        width: 100%;
        background: #fff;
        .consignee{
          display: inline-flex;
          justify-content: space-between;
          width: 100%;
          padding-bottom: 8px;
        }
        .shipping{
          padding-bottom: 8px;
          word-break: break-all;
          word-wrap: break-word;
          border-bottom: 1px solid #f2f2f2;
        }
        .delete{
          position: absolute;
          top:0;
          right: 0;
          height: 100%;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          background: #ff4e00;
          color: #ffffff;
          width: n(0);

        }
        .defaultAddress{
          width: 100%;
          height: n(60);
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          .modify{
            img{
              width: n(40);
              height: n(40);
            }
          }
        }
      }
    }
    .add{
      position: fixed;
      bottom: 2%;
      width: 100%;
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      margin-top: n(50);
      img{
        width: n(80);
        height: n(80);
      }
    }   
  }
</style>




